/**
 * Transforme une Area pour êtr exploité dans un autre contexte.
 */
export interface IResAreaAdapter {
	/** Transforme cette area (issu de la view "main") pour un usage de type sélection de ressource (view "select"). */
	asSelectArea?(): IArea<IRegPointer<IDepotResUiEnv>>

	/** */
	asConsultArea?(): IArea<IRegPointer<IDepotResUiEnv>>
}

/**
 * Area construisant un ensemble d'autres views d'une ressource.
 */
export class ResLayoutArea extends AreaAsync<IRegPointer<IDepotResUiEnv>, ResLayoutView> {

	scroll?: 'small' | 'large'

	body: (IArea<IRegPointer<IDepotResUiEnv>, IView> | IJsTag | string)[]

	constructor(id?: string) {
		super(id || 'resLayout');
		this.requireLib(IO.asEndPoint("/@back@/store/views/resLayoutView.js"));
	}

	setBody(...body: (IArea<IRegPointer<IDepotResUiEnv>, IView> | IJsTag | string)[]): this {
		this.body = body;
		return this;
	}

	setScroll(scroll: 'small' | 'large' | null): this {
		this.scroll = scroll;
		return this;
	}

	buildBody(ctx: IRegPointer<IDepotResUiEnv>, lastDatas?: JLastDatas): ResLayoutView {
		if (ctx.reg.isClosed) return null; //raceCond
		const init: OResLayoutViewInit = {
			area: this,
			areaContext: ctx,
			reg: ctx.reg,
			lastDatas: lastDatas,
			skin: this.getSkin(ctx),
			skinOver: this.getSkinOver(ctx),
			body: this.body,
			scroll: this.scroll
		};
		return <store-res-layout î={init}/> as ResLayoutView;
	}
}

/**
 * Area construisant la view des infos d'une ressource.
 */
export class ResInfoArea extends AreaAsync<IRegPointer<IDepotResUiEnv>, ResInfoView> {

	lines: IResInfosLineDrawer[]

	constructor() {
		super('resInfo');
		this.requireLib(IO.asEndPoint("/@back@/store/views/resInfosView.js"));
	}

	setLines(...lines: IResInfosLineDrawer[]): this {
		this.lines = lines;
		return this;
	}

	addDefaultLines(): this {
		if (!this.lines) this.lines = [];
		this.lines.push(RESINFO.path, RESINFO.url, RESINFO.status, RESINFO.deploy, RESINFO.prc); /*, RESINFO.debug , RESINFO.moved A REVOIR : si en tooltip, fermer le tooltip container ? */
		return this;
	}

	addLines(...lines: IResInfosLineDrawer[]): this {
		if (!this.lines) this.lines = [];
		if (lines) this.lines.push(...lines);
		return this;
	}


	buildBody(ctx: IRegPointer<IDepotResUiEnv>, lastDatas?: JLastDatas): ResInfoView {
		if (ctx.reg.isClosed) return null; //raceCond
		const init: OResInfoViewInit = {
			area: this,
			areaContext: ctx,
			reg: ctx.reg,
			lastDatas: lastDatas,
			skin: this.getSkin(ctx),
			skinOver: this.getSkinOver(ctx),
			lines: this.lines
		};
		return <store-res-infos î={init}/> as ResInfoView;
	}
}


