/**
 * Area construisant la main view standard.
 */
export class ResMainArea extends ResLayoutArea implements IResAreaAdapter {

	contentViews: (IArea<IRegPointer<IDepotResUiEnv>, IView> | IJsTag | string)[]

	static ancArea = new ResAncArea();
	static actionsArea = new ResBarActionsArea(["actions:depot:resMainView:edit"], "resEdit");
	static headerTag = {tag: 'header', children: [ResMainArea.ancArea, ResMainArea.actionsArea]};
	static headerSelectTag = {tag: 'header', children: [ResMainArea.ancArea]};

	/** header standard dans le views main, select...*/
	static titleLine: IResInfosLineDrawer = {
		drawLine(reg: IReg<IDepotResUiEnv>, parent: Node) {
			const ni = reg.env.nodeInfos;
			const resType = reg.env.resType;
			const actions = <c-bar-actions î={{reg, actionContext: reg, actions: reg.getList("actions:depot:resMainView:infos")} as OBarActionsInit<IRegPointer<IDepotResUiEnv>>}/> as BarActions<IRegPointer<IDepotResUiEnv>>;
			const versions = (ni.t !== "moved" ? resType.prcVersionning === "VCS" : ni.hist || ni.succ) && reg.getPref("depot.showVersions", true);
			//console.log("resType.prcVersionning:::",resType.prcVersionning);
			const name = <span class="name">{resType.resName(ni)}</span>;
			const versionInTi = !versions && resType.prcVersionning === "VCB" ? <span id="versionInTi">@<ResVersionsView î={{reg, showDate: false, onlyChangedContent: true} as OResVersionsViewInit}/></span> : undefined;
			let flagInTi: Element | undefined;
			if (ni.trashed) {
				flagInTi = <span class="trashed" title={reg.env.resTypes.visStateDescription(ni)}/>
			} else	if (ni.unlisted) {
				flagInTi = <span class="unlisted" title={reg.env.resTypes.visStateDescription(ni)}/>
			}
			parent.appendChild(<div id="title">
				<img class="icon" src={resType.resIcon(ni)}/>
				<span id="nameCtn">{name}{versionInTi}{flagInTi}</span>
				{versions ? undefined : actions}
			</div>);
			if (versions) {
				parent.appendChild(<div id="version">
					<ResVersionsView î={{reg, showDate: true, onlyChangedContent: true} as OResVersionsViewInit}/>
					{actions}
				</div>);
			}
			const infosArea = resType.resView("infosMain");
			if (infosArea) {
				function addInfosMain(view: HTMLElement) {
					if (!view) return;
					view.id = "infosMain";
					reg.installSkin("store-res-infosMain", view.shadowRoot);
					parent.appendChild(view);
				}

				if (infosArea.needAsync(reg)) {
					infosArea.loadBody(reg).then(addInfosMain);
				} else {
					addInfosMain(infosArea.buildBody(reg));
				}
			}
		}
	}
	static titleArea = new ResInfoArea().setSkin("store-res-titlebar").setLines(ResMainArea.titleLine);

	constructor() {
		super('resMain');
		this._skinOver = "store-res-main";
		this.scroll = "large";
		this.requireLib(IO.asEndPoint("/@back@/store/views/resMainView.js"));
	}

	setContent(...contentView: (IArea<IRegPointer<IDepotResUiEnv>, IView> | IJsTag | string)[]): this {
		this.contentViews = contentView;
		this.body = [
			ResMainArea.headerTag,
			{tag: 'main', children: [ResMainArea.titleArea, ...contentView]}
		]
		return this;
	}

	asSelectArea(): IArea<IRegPointer<IDepotResUiEnv>> {
		if (!this._selectArea) {
			//suppr le ResBarActionsArea ?
			this._selectArea = new ResMainArea();
			this._selectArea.contentViews = this.contentViews.map((cv) => (cv as IResAreaAdapter)?.asSelectArea ? (cv as IResAreaAdapter).asSelectArea() : cv);
			this._selectArea.body = [
				ResMainArea.headerSelectTag,
				{tag: 'main', children: [ResMainArea.titleArea, ...this._selectArea.contentViews]}
			]
		}
		return this._selectArea;
	}

	_selectArea: ResMainArea

	asConsultArea(): IArea<IRegPointer<IDepotResUiEnv>> {
		if (!this._consultArea) {
			//suppr le ResBarActionsArea ?
			this._consultArea = new ResMainArea();
			this._consultArea.contentViews = this.contentViews.map((cv) => (cv as IResAreaAdapter)?.asConsultArea ? (cv as IResAreaAdapter).asConsultArea() : cv);
			this._consultArea.body = [
				{tag: 'main', children: [ResMainArea.titleArea, ...this._consultArea.contentViews]}
			]
		}
		return this._consultArea;
	}

	_consultArea: ResMainArea
}


